.navbar {
    background-color: #007166;
    height: 50px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin: -10px;
    z-index: 5;
}

.menu-bars {
    margin-left: 2rem;
    font-size: 2rem;
    background: none;
    color: #f5f5f5;
    z-index: 5;
}

.nav-menu {
    background-color: #007166;
    width: 300px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    left: -100%;
    transition: 850ms;
    z-index: 5;
}

.nav-menu.active {
    left: 0;
    transition: 350ms;
}

.nav-text {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 8px 0px 8px 0px;
    list-style: none;
    height: 60px;
}

.nav-text a {
    text-decoration: none;
    color: #f5f5f5;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 4px;
}

.nav-text a:hover {
    background-color: #01a595;
}

.nav-menu-items {
    width: 100%;
}

.navbar-toggle {
    background-color: #007166;
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.close-button {
    margin-right: 10px;
}

.topOut {
    width: 100%;
    position: relative;
}